
@charset "UTF-8";
@font-face {
  font-family: 'iconfont';
  src: url("../fonts/icons/turn-arrow.eot") format("embedded-opentype"), url("../fonts/icons/turn-arrow.woff") format("woff"), url("../fonts/icons/turn-arrow.ttf") format("truetype"), url("../fonts/icons/turn-arrow.svg") format("svg");
  /* iOS 4.1- */
}
html, body, #app {
  height: 100%;
  width: 100%;
  background: #333;
}
img {
  width: 100%;
}

/*页面整体*/
.stage {
  height: 100%;
  width: 100%;
  position: relative;
}

/*图片控件样式*/
.img-sec {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.img-figure {
    position: absolute;
    top: 0;
    left: 0;
    width: 320px;
    height: 360px;
    margin: 0;
    padding: 40px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #fff;
    cursor: pointer;
    -webkit-transform-origin: 0 50% 0;
            transform-origin: 0 50% 0;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
    transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
}
.img-figure.is-inverse {
      -webkit-transform: translate(320px) rotateY(180deg);
              transform: translate(320px) rotateY(180deg);
}
.img-back {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    color: #666;
    -webkit-transform: rotateY(180deg) translateZ(1px);
            transform: rotateY(180deg) translateZ(1px);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: #fff;
}
figcaption {
    text-align: center;
}
figcaption .img-title {
      margin: 20px 0 0 0;
      color: #a7a0a2;
      font-size: 16px;
}

/*导航控件样式*/
.controller-nav {
  position: absolute;
  left: 0;
  bottom: 20px;
  z-index: 101;
  width: 100%;
  text-align: center;
}
.controller-unit {
    display: inline-block;
    width: 30px;
    height: 30px;
    margin: 0 10px;
    text-align: center;
    cursor: pointer;
    background-color: #aaa;
    border-radius: 50%;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    vertical-align: middle;
    -webkit-transition: -webkit-transform .6s ease-in-out;
    transition: -webkit-transform .6s ease-in-out;
    transition: transform .6s ease-in-out;
    transition: transform .6s ease-in-out, -webkit-transform .6s ease-in-out;
}
.controller-unit.is-center {
      background: #888888;
      -webkit-transform: scale(1);
              transform: scale(1);
}
.controller-unit.is-center::after {
        color: #fff;
        font-family: 'iconfont';
        line-height: 30px;
        content: '\e600';
}
.controller-unit.is-center.is-inverse {
        background: #555;
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
}

/*canvas*/
canvas {
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  pointer-events: none;
}



